@import "src/styles/index";

.section {
  position: relative;
  padding: 33px 0 27px;
  @include iPadMini {
    padding-top: 46px;
    height: auto;
  }
}

.subList {
  display: none;
  transition: all 1s;
}

.list {
  row-gap: 20px;
  & li {
    display: flex;
    flex-direction: column;
    &:hover {
      cursor: pointer;
      .icon {
        transform: rotate(-90deg);
      }
      .subList {
        display: block;
      }
    }
  }
}

.wrapperLink {
  display: flex;
  align-items: center;

  @include mobile {
    justify-content: space-between;
  }
}

.link {
  display: block;
  font-size: 30.5px;
  font-weight: bold;
  line-height: 1.64;
  text-align: left;
  color: #39373a;
}

.icon {
  margin-left: 8px;
  font-size: 30.5px;

  transform: rotate(90deg);
  transition: all 0.8s;
}

.subLink {
  width: fit-content;
  font-size: 25px;
  line-height: 1.64;
  text-align: left;
  color: #39373a;
  border-bottom: 1px solid #39373a;
}
