@import "src/styles/index";

.section {
  position: relative;

  background-image: url("../../../assets/images/main/mainImg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  margin: 0 auto;

  height: 700px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  padding-bottom: 46px;
  background-blend-mode: darken;
  box-shadow: inset 0px -20px 150px 0px;

  @include iPadMini {
    padding-top: 46px;
    height: auto;
  }
}

.groupBlocks {
  display: flex;

  @include iPadMini {
    flex-wrap: wrap;
    padding: 0 15px;
    row-gap: 15px;
  }
}

.block {
  width: 25%;
  text-align: center;
  @include mobile {
    width: 100%;
  }
}
.wrapperIcon {
  width: 102px;
  height: 102px;
  margin: auto;
  border: 1px solid #fff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.text {
  max-width: 250px;
  font-size: 23.5px;
  font-weight: 300;
  color: #fff;
  @include mobile {
    max-width: 100%;
  }
}
